<template>
    <div class="details">
        <div>
            <span>{{ann.title}}</span>
            <span>{{ann.price}} zł</span>
        </div>
        <div>{{ann.address}}</div>
        <img :src="ann.imgUrl"/>
        <div>
           {{ann.description}}
        </div>
    </div>
</template>

<script>
    import { mapState } from "vuex";

    export default {
      name: "details",
      computed: mapState({
        ann: state =>
          state.announcments.announcments.find(
            ann => ann.id === state.announcments.currentAnnouncmentId
          ) || {},
      })
    };
</script>

<style lang="sass" scoped>
    .details
        display: flex
        flex-direction: column
    
</style>
